import "./contact.scss"
import gt from "./github.png"
import lkdn from "./linkedin.png"
import insta from "./instagram.png"
import twitter from "./twitter.png"

function Contact({ light }) {
    return (
        <div className="Contact">
            <div className="contact-box">
                <p className={light == "light" ? "contact-desc contact-desc-light" : "contact-desc"} >
                    Thank you for visiting my portfolio! While I prioritize email as my primary means of communication, you can also connect with me on various social platforms. Feel free to reach out with any inquiries, collaborations, or just to say hello via email. I check my inbox regularly and will respond as promptly as possible. Additionally, you can find me on GitHub, LinkedIn, Instagram, and Twitter, where I occasionally share updates and insights. I'm looking forward to connecting with you on these platforms and exploring opportunities together
                </p>

                <div className="contact-buttons">
                    <div className="mail-button">
                        <a target="blank" href="mailto:sahilkalkal108@gmail.com">
                            <button className={light == "light" ? "button-64 button-64-light" : "button-64"} role="button"><span className="text">Mail</span></button>
                        </a>
                    </div>
                    <div className="mail-button">
                        <a target="blank" href="https://linkmap.vercel.app/sahilkalkal">
                            <button className={light == "light" ? "button-64 button-64-light" : "button-64"} role="button"><span className="text">Socials</span></button>
                        </a>
                    </div>
                    {/* <div className="other-buttons">
                        <a href="https://github.com/sahilkalkal9" target="blank">
                            <img className={light == "light" ? "cimg cimg-light" : "cimg"} src={gt} />
                        </a>
                        <a href="https://www.linkedin.com/in/sahil810/" target="blank">
                            <img className={light == "light" ? "cimg cimg-light" : "cimg"} src={lkdn} />
                        </a>
                        <a href="https://www.instagram.com/sahil.kalkal_" target="blank">
                            <img className={light == "light" ? "cimg cimg-light" : "cimg"} src={insta} />
                        </a>
                        <a href="https://www.twitter.com/sahilkalkal_" target="blank">
                            <img className={light == "light" ? "cimg cimg-light" : "cimg"} src={twitter} />
                        </a>
                        
                    </div> */}
                </div>
            </div>
        </div>
    )
}

export default Contact