import "./home.scss"
import backI from "./backI.jpg"
import Projects from "../projects/projects"
import HomeProjects from "./home-projects/projects";
import Skills from "./skills/skills";
import Tools from "./tools/skills";
import { Link } from "react-router-dom";
import MyResume from "./resume.pdf"
import GoToTop from "../../top";
import Contact from "../contact/contact";

function Home({ light }) {
    var grd = "linear-gradient(90deg, rgb(230, 65, 87) 0%, rgb(65, 93, 230) 100%)"
    return (
        <div className="Home">
            <div className="home-display">
                <div className="home-right">
                    <p className={light == "light" ? "name name-light" : "name"} >Sahil Kalkal</p>



                    <p className={light == "light" ? "bio bio-light" : "bio"} >
                        A student who loves to write code.
                        Basically, I am a web developer. Welcome to my digital garden, explore my garden to know about me.<br />
                        Btw I write blogs too.
                    </p>


                    <div className="home-buttons">
                        <Link to="/about" >
                            <button className={light == "light" ? "about-button about-button-light" : "about-button"} >About me</button>
                        </Link>
                        <a
                                href={MyResume}
                                download="Sahil_Resume"
                                target="_blank"
                                rel="noreferrer"
                            >
                        <button className={light == "light" ? "resume-button resume-button-light" : "resume-button"} >Resume</button>
                        </a>
                    </div>
                </div>
                <img className="backI" src={backI} alt="developer" />
            </div>
            <div className="projects-display">
                <p className={light == "light" ? "projects-display-heading projects-display-heading-light" : "projects-display-heading"} >Projects</p>
                <div className="view-all-box">
                    <Link to="/projects">
                        <button className={light == "light" ? "view-button view-button-light" : "view-button"} >View all</button>
                    </Link>
                </div>
                <HomeProjects light={light} />

            </div>
 
            <div className="skills-display">
                <p className={light == "light" ? "projects-display-heading projects-display-heading-light" : "projects-display-heading"} >Skills</p>
                <Skills light={light} />
            </div>
            <div className="skills-display">
                <p className={light == "light" ? "projects-display-heading projects-display-heading-light" : "projects-display-heading"} >Tools</p>
                <Tools light={light} />
            </div>

            <div className="contact-display">
                <p className={light == "light" ? "projects-display-heading projects-display-heading-light" : "projects-display-heading"} >Contact</p>
                <Contact light={light} />
            </div>

            <GoToTop />
        </div >
    )
}
export default Home;