
import ct from "./chemictionary.png"
import strayfarm from "./strayfarm.png"
import resume from "./resume.png"
import todo from "./todo.png"
import gsign from "./google.png"
import fauth from "./auth.png"
import socioease from "./socioease.png"
import iland from "./instagram.png"
import star from "./star.png"
import pt from "./pt.png"
import fi from "./folders.png"
import tac from "./tac.png"

import nt from "./notes.png"

import React, { useEffect, useState } from 'react';

import linkmap from "./marketing.png"

function HomeProjects({ light }) {

    const [starsChem, setStarsChem] = useState(null);
    const [starsNotebook, setStarsNotebook] = useState(null);
    const [starsFile, setStarsFile] = useState(null);
    const [starsFolio, setStarsFolio] = useState(null);
    const [starsStray, setStarsStray] = useState(null);
    const [starsResume, setStarsResume] = useState(null);
    const [starsTodo, setStarsTodo] = useState(null);
    const [starsGoogle, setStarsGoogle] = useState(null);
    const [starsInsta, setStarsInsta] = useState(null);
    const [starsSocioEase, setStarsSocioEase] = useState(null);
    const [linkMap, setLinkmap] = useState(null);



    // console.log(stars)




    const projects = [
        {
            "name": "TacTime",
            "src": tac,
            "stars": linkMap,
            "year": "2023",
            "tagline": "Multiplayer tic-tac-toe",
            "desc": "TacTime is a simple online multiplayer tic-tac-toe game.",
            "code": "https://github.com/sahilkalkal9/tactime",
            "star": "https://github.com/sahilkalkal9/tactime",
            "demo": "https://tactime.vercel.app",
            "br": "100px",
            "tech": "React | CSS | Firebase"
        },
        {
            "name": "LinkMap",
            "src": linkmap,
            "stars": linkMap,
            "year": "2023",
            "tagline": "Group your links",
            "desc": "LinkMap is a simple web application to group your links and then you can share them via one single link.",
            "code": "https://github.com/sahilkalkal9/linkmap",
            "star": "https://github.com/sahilkalkal9/linkmap",
            "demo": "https://linkmap.vercel.app",
            "br": "100px",
            "tech": "React | CSS | Firebase"
        },
        {
            "name": "SocioEase",
            "src": socioease,
            "stars": starsSocioEase,
            "year": "2023",
            "tagline": "A social media website",
            "desc": "SocioEase is a simple social media website which is strictly concerned about your privacy and it is distraction free also. (Under Development)",
            "code": "https://github.com/sahilkalkal9/socioease",
            "star": "https://github.com/sahilkalkal9/socioease",
            "demo": "https://socioease.vercel.app",
            "br": "100px",
            "tech": "React | CSS | Firebase"
        }





    ]

    return (
        <div className="ProjectsH">

            <div>
                <div className="projects-container">

                    {
                        projects.map((pt) => (

                            <div className={light == "light" ? "project project-light" : "project"} >
                                <div className="project-dets">
                                    <div className="project-head">
                                        <p className={light == "light" ? "project-year project-year-light" : "project-year"} >{pt.year}</p>
                                        <a target="blank" href={pt.code}>
                                            <div className="header-project">
                                                <img className="star" src={star} />
                                                <p className="project-year starss" >{pt.stars}</p>
                                            </div>
                                        </a>

                                    </div>
                                    <div className="project-logo-container">
                                        <img src={pt.src} className="project-logo" alt="project logo" style={{ borderRadius: pt.br }} />
                                        <div className="project-name-container">
                                            <a target="blank" href={pt.code} ><p className={light == "light" ? "project-name project-name-light" : "project-name"}>{pt.name}</p></a>
                                            <p className={light == "light" ? "project-tagline project-tagline-light" : "project-tagline"}>{pt.tagline}</p>
                                        </div>
                                    </div>
                                    <p className={light == "light" ? "project-desc project-desc-light" : "project-desc"} >{pt.desc}</p>
                                    <p className={light == "light" ? "tech tech-light" : "tech"}>{pt.tech}</p>
                                </div>

                                <br />
                                <div className="project-buttons">




                                    <a href={pt.demo} target="blank"><div className={light == "light" ? "code code-light" : "code"}>
                                        <p className={light == "light" ? "code-link code-link-light" : "code-link"}>Visit</p>
                                        {/* <img className="redirect predp" alt="redirect icon" src={redi} /> */}
                                    </div></a>
                                </div>

                            </div>

                        ))
                    }

                </div>
            </div>
        </div>

    )
}
export default HomeProjects;