import { Route, Routes } from 'react-router-dom';
import './App.scss';
import Home from './components/home/home';
import Nav from './components/nav/nav';
import About from './components/about/about';
import Projects from './components/projects/projects';
import Blogs from './components/blogs/blogs';
import dark from "./dark.png"
import light from "./light.png"
import { useEffect, useState } from 'react';

function App() {
  const [theme, setTheme] = useState("")
  const [src, setSrc] = useState(null)

  const changeIt = () => {
    const newTheme = theme === 'dark' ? 'light' : 'dark';
    const newSrc = theme === 'dark' ? dark : light;
    localStorage.setItem('theme', newTheme);
    localStorage.setItem('src', newSrc);
    setTheme(newTheme)
    setSrc(newSrc)
  };


  useEffect(() => {
    const storedTheme = localStorage.getItem('theme');
    const storedSrc = localStorage.getItem('src');
    if (storedTheme && storedSrc) {
      setTheme(storedTheme)
      setSrc(storedSrc)
    }
    else {
      setTheme("dark")
      setSrc(light)

    }

  })

  return (
    <div className={theme == "light" ? " App App-light" : "App"} >
      <Nav changeIt={changeIt} light={theme} source={src} />
      <Routes>
        <Route path='/' element={<Home light={theme} />} />
        <Route path='/about' element={<About light={theme} />} />
        <Route path='/projects' element={<Projects light={theme} />} />
        <Route path='/blogs' element={<Blogs light={theme} />} />
      </Routes>
    </div>
  );
}

export default App;
