import "./nav.scss"
import { Link, useLocation } from "react-router-dom";
import me from "./me.jpg"
import menuG from "./menu.gif"

function Nav({ changeIt, light, source }) {

    const location = useLocation()
    const currentPath = location.pathname;
    const isAbout = currentPath == "/about"
    const isProjects = currentPath == "/projects"
    const isBlogs = currentPath == "/blogs"

    const openNav = () => {
        document.getElementById("overlayNav").style.width = "100%"
    }

    const closeNav = () => {
        document.getElementById("overlayNav").style.width = "0%"
    }


    return (
        <>
            <div id="overlayNav" className="overlay-nav">

                <div className="overlay-menu">
                    <button className="close-button" onClick={closeNav} >Close</button>
                    <Link to="/about">
                        <p onClick={closeNav} className={isAbout ? "menu-itemO menu-item-navO" : "menu-itemO"}>About</p>
                    </Link>
                    <Link to="/projects" >
                        <p onClick={closeNav} className={isProjects ? "menu-itemO menu-item-navO" : "menu-itemO"}>Projects</p>
                    </Link>
                    {/* <Link to="/blogs"  >
                        <p onClick={closeNav} className={isBlogs ? "menu-itemO menu-item-navO" : "menu-itemO"}>Blogs</p>
                    </Link> */}
                </div>
            </div>
            <div className="Nav">
                <div className="Logo">
                    <Link to="/">
                        <img className="logo" src={me} />
                    </Link>
                </div>
                <div className="menu">
                    <Link to="/about">
                        <p className={
                            light == "light"
                                ? (
                                    isAbout ? "menu-item menu-item-light menu-item-nav" : "menu-item menu-item-light"
                                ) : isAbout ? "menu-item menu-item-nav" : "menu-item"
                        }>About</p>
                    </Link>
                    <Link to="/projects" >
                        <p className={
                            light == "light"
                                ? (
                                    isProjects ? "menu-item menu-item-light menu-item-nav" : "menu-item menu-item-light"
                                ) : isProjects ? "menu-item menu-item-nav" : "menu-item"
                        }>Projects</p>
                    </Link>
                    {/* <Link to="/blogs" >
                        <p className={
                            light == "light"
                                ? (
                                    isBlogs ? "menu-item menu-item-light menu-item-nav" : "menu-item menu-item-light"
                                ) : isBlogs ? "menu-item menu-item-nav" : "menu-item"
                        } >Blogs</p>
                    </Link> */}
                    <img className="mode" onClick={changeIt} src={source} />
                    <img id="menuG" onClick={openNav} className="menuG" src={menuG} />

                </div>
            </div>
        </>
    )
}
export default Nav;