import GoToTop from "../../top";
import HomeProjects from "../home/home-projects/projects";
import Skills from "../home/skills/skills";
import "./about.scss"

function About({ light }) {
    return (
        <div className="About">
            <p className={light == "light" ? "projects-display-heading projects-display-heading-light" : "projects-display-heading"} >About</p><br /><br />
            <div className={light == "light" ? "about-me-box about-me-box-light" : "about-me-box"}>
                <p className={light == "light" ? "introduction introduction-light" : "introduction"} >#Introduction</p>
                <p className={light == "light" ? "about-det about-det-light" : "about-det"} >
                    Hello, I am Sahil Kalkal, 21 year old, from India. At present, I live in New Delhi, india. I am a web developer, with a experience of 4 years in the field of web development. I am very much passionate towards my work and always open to gain new skills. Currently I am pursuing my bachelor's degree in the field of computer science engineering from Maharshi Dayanand University, Rohtak, Haryana, India.<br />
                    I started web development in my 11th standard. I have built so many websites for different purposes and I have good knowledge in the field of web development. I use react.js for web development, react.js is a framework of JavaScript used for web development. I also know a little in the field of back end web development, I use only firebase for the back end and looking forward to expanding my knowledge in the field of web development.
                </p>
                <br /><br />
                <p className={light == "light" ? "introduction introduction-light" : "introduction"} >#What I'm doing</p>
                <p className={light == "light" ? "about-det about-det-light" : "about-det"} >
                    Nowadays I am looking forward to expanding my knowledge in the field of web development, therefore I learn new things daily and do practice on daily basis.
                    <br /><br />
                    Currently, I am working on SocioEase. SocioEase is a social media platform.
                    I am building it using react.js and firebase.
                    My aim is to make SocioEase a simple, neat and clean social media platform where people can connect with their friends, people can see their friends' posts, people can post their thoughts, people can chat with their friends, our main concern is the privacy and there should be no distractions, I want to make this platform private for each and everyone like only friends can send messages and I don't want to make it a Instagram or any social media platform's clone, I just want to make it private and distraction free. There will be no explore section, I know 90% people want to follow their favourite influencers, want to scroll reels but on the other side 10% people just want to connect with their friends/family and to know what's going in their life, they can see their friends' activity/posts, I think it is the efficient way to avoid distractions. <br /><br />
                    <div className="about-buttons">
                        <a href="https://socioease.vercel.app" target="blank"><div >
                            <div className={light == "light" ? "view-button view-button-light" : "view-button"}>Visit SocioEase</div>
                        </div></a>
                    </div>
                    <br />
                    And I am also working on the Chemictionary, I am founder of the Chemictionary.
                    Chemictionary is a simple, fast and easy platform to build the concepts of Chemistry.
                    Chemictionary - Dictionary of Chemistry. <br />
                    As chemistry is my favourite subject and when I was in secondary school, I felt like there is no efficient online resource to clear the concepts and to solve the doubts. Therefore, I decided to start the Chemictionary - Dictionary of Chemistry, here a student can build a better understanding of a concept.<br /><br />
                    <div className="about-buttons">
                        <a href="https://chemictionary.com" target="blank"><div>
                            <div className={light == "light" ? "view-button view-button-light" : "view-button"}>Visit Chemictionary</div>
                        </div></a>
                    </div>
                </p>
                <br /><br />
                <p className={light == "light" ? "introduction introduction-light" : "introduction"} >#What I'm learning</p>
                <p className={light == "light" ? "about-det about-det-light" : "about-det"} >
                    Nowadays I am learning CSS like smooth animations in CSS, responsive UI in CSS, smooth design in CSS and react.js advanced concepts, basically I am learning new things to expand my knowledge in the front end web development.<br />
                    And in the field of back end web development, I know a little about Firebase but I think SQL is much better than Firebase in many aspects, therefore I am learning SQL too for the back end.Learning node.js too.
                </p>
                <br /><br />
                <p className={light == "light" ? "introduction introduction-light" : "introduction"} >#Experience</p>
                <p className={light == "light" ? "about-det about-det-light" : "about-det"} >
                    As I am a student, I do not have any professional experience, I have a self experience of three years from 2020 to 2023 and I have built so many websites for different purposes.
                </p>
                {/* <br /><br />
                <p className={light == "light" ? "introduction introduction-light" : "introduction"} >Skills</p>
                <Skills light={light} />
                <br /><br />
                <p className={light == "light" ? "introduction introduction-light" : "introduction"} >Projects</p>
                <div className="view-all-box">
                    <button className={light == "light" ? "view-button view-button-light" : "view-button"} >View all</button>
                </div>
                <HomeProjects light={light} />*/}
                <br /><br /><br />
                <p className={light == "light" ? "about-det about-det-light" : "about-det"} >
                    Have a great day ahead!!
                    <br /> < br />
                    Thank you < br />
                    Sahil Kalkal
                </p>
                <br />
            </div>
            <GoToTop />
        </div>
    )
}

export default About;